ậy.")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("9. Tối 7h ăn ít, cả ngày (0h-24h) ăn 250ml, 11h ngủ, ko dậy làm sao? Tl: đã 4h từ cữ trước, mẹ lén cho ăn lúc ngủ, vì ngày\n      ăn ko đủ 300ml. Nếu 11h ko dậy, mẹ lén cho ăn bình. Luật mời như ban ngày (mời 1 hoặc 2 lần tối đa), nếu cả 2 lần mời đều ko ăn mẹ có\n      thể cho ti mẹ để đủ 300ml. Nếu ko ăn, chờ cữ kế. Nếu ăn bình rồi thì ko cho ti mẹ, ăn bình rồi vẫn thiếu 300ml thì chờ cữ kế.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("10. Tối 7h ăn ít, 11h mẹ lén cho ăn đủ 300ml, 2h sáng dậy ẹ ẹ, làm sao? Tl: đã đủ 3h từ cữ trước (11h mẹ lén cho bú). Bật\n      đèn ngủ lên cho tỉnh, nếu bé mở mắt nhìn bình thì coi như tự ăn. Nếu bé khóc/lăn lộn, có dấu hiệu đói nhưng vẫn nhắm mắt, mẹ có\n      thể cho ăn luôn. (Đêm, nếu bé đang bú mà ngủ luôn cũng được ko cần rút bình, nếu bé dậy khóc nhưng vẫn nhắm mắt cũng ok.) Xem bài 9 để\n      biết nên cho bé ăn mấy cữ đêm. Cữ đêm luôn luôn dãn xa hơn cữ ngày.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("11. 2h sáng ăn, 3h sáng dậy khóc dỗ ko nín, có mời ko? Tl: có mời, dỗ ko nín là phải mời.")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("12. Con đói quá khóc to nhắm mắt ko nhìn bình làm sao? Tl: thử dỗ nín, ko nín thì từ từ đưa bình cạ mồm con, con muốn ăn\n      sẽ ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("13. Ngày, con bắt đầu bú bình thì buồn ngủ, làm sao? Con đang bú thì ngủ mất, làm sao? Tl: xoa tay chân, nói chuyện, cho\n      con tỉnh. Ko hát hò dụ tv đồ chơi. Con ngủ mất thì rút bình ra, mời lại. Ko ăn là cất bình nếu đã mời 2 lần.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("14. Con đang bú, nhai núm ngậm, mút nhả mút nhả, làm sao? Tl: NHÈ dẹp luôn. hoặc xem bài 7 8")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("15. Lần mời 2 của các cữ con ăn ít 10, 15ml) hoặc ko ăn, làm sao? Tl: bỏ lần mời 2. Nếu mời lần 2 con sẽ tưởng mẹ ép con\n      ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("16. Có cần vỗ ợ ko? Vỗ ợ lúc nào? Vỗ bao lâu? Tl: có. Vỗ 5 phút trước khi ăn. Và giữa 2 lần mời. 10-15'")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("17. Giữa 2l mời chờ bao lâu thì tốt? Tl: tùy. Có bé 20f. Có bé chịu 15f. Thử mới biết.")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("18. Câu hỏi rất khó: 7h mời ko ăn. 11h con nằm chơi ko đói, mời ko ăn thì làm sao????? Tl: chờ đến cữ sau. Mẹ có làm được\n      ko????? Phải kiên trì nếu muốn con ăn ngoan. Ko theo đúng luật thì ko cần làm vì sẽ ko khỏi bệnh. Con chỉ bị bỏ đói vô ích thôi\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Mẹ cố gắng làm đúng luật, vì tất cả các tiểu tiết trong bài đều quan trọng, có liên quan đến nhận thức\n      và tâm lý của trẻ.\n    ")])])])}]

