var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"_39k5 _5s6c"},[_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Kỷ luật bàn ăn:")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Phương pháp chỉ có 1: cho con đói. Nhưng mẹ bỏ đói con phải có nguyên tắc, không phải đói là ăn. Bé nào bị\n      biếng ăn tâm lý (BATL) do bị ép ăn thì đói cũng không ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Nguyên nhân BATL:")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("1. Mẹ la mắng đánh con khi con ăn chậm biếng ăn, làm con sợ hãi")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("2. Mẹ dụ dỗ cố đút thêm vài muỗng cho hết bát khi con đã no")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("3. Lịch sinh hoạt ko hợp lý. Sữa, 2 tiếng sau dặm, 2 tiếng sau lại sữa. Mẹ cho ăn khi con chưa đói, con ăn ít,\n      hoặc không ăn, mẹ nài nỉ lừa dụ thêm vài muỗng. Cho con ăn phải theo nhu cầu, ko theo giờ. Bé đói mới cho ăn thì sẽ ăn ngoan.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("4. Mẹ cho xem tv, phone, đồ chơi, mất tập trung, con ko tự chủ khi mẹ đút, mẹ nghĩ làm vậy con ăn nhanh hơn,\n      đỡ mất thời gian hơn nhưng tác hại về sau rất lớn. Nhất là bé nào ko có đồ chơi thì ko ăn, mà có đồ chơi mới ăn thì 100% là ép ăn\n      nhé. (dạng này là ép ăn nhẹ, thường các bé vẫn sẽ hợp tác khi có đồ chơi, bế đi rong, phone v.v. nhưng khi lớn hơn và khả năng tập\n      trung cao hơn, bé sẽ bỏ ăn/ăn ít)\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Đây là những gì mình tổng kết ra từ con mình. Cách làm có thể có tác dụng với một số bé. Mẹ nào muốn\n      có thể thử.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Kỷ luật bàn ăn: ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("1. Đặt con vào ghế ăn, con khóc quấy, mím môi, quay đầu, không hợp tác, hoặc có bất kỳ thói xấu nào, lập\n      tức giải thích với con và đưa ra yêu cầu của mẹ. Nếu con không làm theo, bế ra ngoài, dọn đồ ăn không cho con nhìn thấy nữa. (thói\n      xấu ví dụ như: không chịu mặc yếm, không chịu ngồi yên, đứng trên ghế, kén chọn ko ăn rau/thịt, ném thức ăn v.v. tật nào mẹ thấy ko\n      tốt thì nên kỷ luật)\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("2. Bế con ra, cất đồ ăn, giải thích (ví dụ: con ko chịu mặc yếm thì nói với con, con muốn ăn phải mặc yếm\n      vào. Hoặc con ngậm lâu ko nuốt, nói với con nếu con ko muốn ăn, mẹ sẽ dọn dẹp). Hãy bình tĩnh khi nói với con, ko la mắng đánh con.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("3. Có thể giảm sữa (ko cắt hoàn toàn) vào ban ngày. Cữ sữa trước khi ngủ buổi tối hãy cho con ăn no, ngủ\n      đêm khoẻ con mới có sức ăn vào hôm sau. Nếu đêm con thức và khóc, ko dỗ ngủ lại được, hãy cho 1 ít sữa thôi để lót dạ. Con ngủ đủ\n      giấc hôm sau sẽ ăn khi đói.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("4. Mẹ phải nhất quán. Con sẽ hiểu ko muốn ăn mẹ ko ép, con sẽ phải nhịn đói đến cữ ăn sau. Cố gắng ko cho\n      con ăn vặt, có thể dời giờ ăn cữ sau lên sớm 1 chút nếu con quá đói vì cả ngày ăn ít. Mẹ hãy kiên trì, vì khi thói xấu đã tồn\n      tại quá lâu (vì mẹ xót con) thì sẽ rất khó sửa. Một khi mẹ nhận ra thói xấu nào nên kỷ luật con ngay lập tức. Thì rất nhanh con\n      sẽ hiểu ra được mẹ không muốn mình làm như thế. Thường con mình khi bị kỷ luật chỉ trong vòng 1 2 ngày bé sẽ hiểu và làm theo ý\n      mẹ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("5. Với bé nào kén chọn, mẹ có thể mang món bé ko chịu ăn lên trước, nếu bé không ăn thì sẽ bị đói. Khi\n      đủ đói bé sẽ ăn. Đây phải là món bé đã từng ăn được rồi và giờ không thích nữa. Nếu là 1 món hoàn toàn mới lạ bé chưa bao giờ\n      ăn, mẹ không nên hy vọng con sẽ ăn ngay lần đầu tiên con thấy. Cần cho bé thời gian làm quen món mới.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("6. Mẹ cũng có thể chọn cách kỷ luật nhân từ hơn, đem 1 suất thức ăn gồm đủ 3 loại tinh bột, đạm, xơ nhưng\n      mỗi món chỉ 1 ít. Nếu bé ăn hết suất này, có thể cho thêm 1 suất nữa giống vậy. Khi bé kén chọn và bỏ món mình ko thích, thì xem\n      như đã ăn no. Mẹ có thể giảm sữa ban ngày. Khi bé đủ đói bé sẽ hiểu mình cần ăn hết tất cả đồ ăn trên bàn thì mẹ mới đem ra 1\n      suất ăn khác.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("7. Để bé ăn nhanh hơn, mẹ có thể đặt chuông 30f. Chuông nên để xa bàn ăn. Mẹ nói với con khi chuông reo là hết\n      giờ ăn. Đúng 30f, mẹ hãy dọn dẹp và ngưng bữa. Nếu chưa đến 30f mà con từ chối ăn mẹ cũng có thể ngưng bữa. Không nên cho con ăn quá\n      lâu trong 1 bữa ăn vì sẽ lấn sang bữa ăn kế tiếp.\n    ")])])])}]

