var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"_39k5 _5s6c"},[_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Đảo lộn ngày đêm:")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Khi còn ở trong bụng mẹ, ban ngày mẹ đi lại nhiều giống như ru ngủ con. Ban đêm mẹ nằm im thì nhiều bé lại\n      tỉnh táo đạp mẹ. Thành ra khi đẻ, trong tháng nếu ko giúp bé điều chỉnh lại, thì bé sẽ bị lẫn lộn ngày đêm.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Trong tháng đầu,")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Khi con dậy khóc đòi ăn ban ngày, mẹ nên cho ăn trong môi trường ánh sáng tốt, nên gọi con, nói chuyện xoa chân\n      tay cho tỉnh táo, cũng có thể lột đồ lột tã ra, lạnh sẽ tỉnh để bú và bú tốt hơn là lơ mơ ngủ bú. Ban ngày cần phơi nắng, cần\n      ồn ào để con biết là ban ngày. Nên cho Vitd3 nhé. Ánh sáng mặt trời sẽ giúp điều tiết cơ thể và giúp con phân biệt ngày đêm. (xem\n      bài 5, coi tín hiệu no và đói của bé dưới 3th)\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Ban đêm, khi con dậy khóc đòi ăn, nên cho ăn trong ánh sáng yếu, đèn mờ, không nói chuyện đùa giỡn, cho ăn xong\n      vỗ ợ rồi quấn lại ngủ luôn. Nên quấn bằng khăn to như bệnh viện ấy, quấn như vầy thì con sẽ ngủ tốt hơn vì ko bị moro reflex ảnh\n      hưởng (giơ 2 tay chân lên khi giật mình dậy). Moro reflex phải khoảng 2-3 tháng mới hết. Nên tháo quấn từng tay/chân 1 lúc 2.5-3 tháng.\n      Bé 3th biết lật thì ko được quấn nữa vì sẽ ngộp thở.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Đảo lộn ngày đêm nói nôm na thì là bị jetlag, tức là mẹ ở vn, mà đi du lịch nước ngoài trái múi giờ thì\n      mẹ ko quen thôi. Có khi ban ngày mẹ cố thức rồi, nhưng ban đêm mẹ vẫn ko ngủ được (vì quá mệt, vì cơ thể chưa quen được với việc đảo\n      lộn lại giờ thức/ngủ khi đi du lịch). Sau 1 thời gian cố gắng thức ban ngày thì mẹ sẽ tự điều chỉnh và ngủ ban đêm (rồi quay về vn\n      thì lại bị trái múi giờ "),_c('span',{staticClass:"_47e3 _5mfr",attrs:{"title":"Biểu tượng cảm xúc winktongue"}},[_c('img',{staticClass:"img",attrs:{"height":"18","role":"presentation","src":"https://static.xx.fbcdn.net/images/emoji.php/v9/t41/2/18/1f61c.png","width":"18","alt":""}}),_c('span',{staticClass:"_7oe",attrs:{"aria-hidden":"true"}},[_vm._v(";p")])]),_vm._v("). Việc đổi giờ giấc ngủ khó hơn việc ăn vì nó liên quan đến đồng hồ sinh học của\n      cơ thể. Cần phải kiên trì chỉnh từng chút 1 từng chút một mỗi ngày chứ ko thể đùng 1 cái bắt bé thức 12h ban ngày và bé sẽ tự\n      ngủ ban đêm. Mệt quá (overtired) chỉ làm con khó ngủ hơn thôi.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Bé này 3th, mẹ ko chỉnh con khi con trong tháng nên bị lẫn lộn. Cho ăn không đúng cách, ko dãn cữ đủ xa nên\n      con ăn lắt nhắt, mời ép nhiều nên biếng ăn bỏ bú. Bé này đã lớn, nên cần kéo thời gian thức ban ngày để hỗ trợ bé ngủ lại ban\n      đêm. Theo mình thì đây là trường hợp duy nhất nên và cần kéo thời gian thức, hỗ trợ bé ngủ đêm.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Tình trạng bé trước khi vào nhóm:"),_c('br'),_vm._v(" ⁃ Đêm thức tới 2-3h sáng, hoặc ngủ từ 11h-2h sáng rồi dậy thức tới sáng "),_c('br'),_vm._v(" ⁃ Ngày\n      ngủ li bì gọi cách nào cũng ko dậy"),_c('br'),_vm._v(" ⁃ Trc rất háu ăn, 1m ăn 100-110/cữ ngày 7-8 cữ, nhưng từ khi ra tháng ăn ít dần 90-80-50-40 ngày 6-7 cữ."),_c('br'),_vm._v(" "),_c('span',{staticClass:"_5mfr"},[_c('span',{staticClass:"_6qdm",staticStyle:{"height":