 con ngủ nap đủ, cần hỗ trợ con\n      ngủ thêm 1 xíu. Nếu đêm ngủ quá ít, ít hơn 10 11 tiếng tuỳ tuổi, tức là con ngủ nap quá nhiều, mẹ cần giảm thời gian ngủ nap lai,\n      làm sao cho con cân bằng giấc ngủ ngày/đêm, ko bị mệt trong ngày vào giờ ăn thì LSH đã hợp lý ko cần chỉnh sửa gì)\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Nên nhớ, ăn phải cho ăn theo nhu cầu, "),_c('span',{staticClass:"_4yxo"},[_vm._v("NGỦ CŨNG PHẢI CHO NGỦ THEO NHU CẦU")]),_vm._v(". Sức ngủ của\n      mỗi bé mỗi khác, LSH của con người ta ko thể áp dụng cho con mình, phải tuỳ theo con mình mà làm. Đói cho ăn, Mệt cho ngủ, Ngủ đủ\n      thì cho dậy. ĐỪNG KÉO THỜI GIAN THỨC WT BỪA BÃI, CHỈ LÀM CON MỆT QUÁ KO NGỦ ĐƯỢC MÀ THÔI. Không phải con người ta 3 tháng phải thức\n      2 tiếng ngủ 2 tiếng thì con mình cũng phải theo được cái LSH đó. Con tui 3 tháng nó chỉ thức được tầm 1 tiếng ngủ 2 hoặc hơn 2\n      tiếng. Chiều chiều thì có khi thức lâu hơn 1 tiếng (nhưng tui ko nhớ nữa rồi).\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Còn Mom nào con hay ngủ dậy trễ và thức khuya, có thể thử dời giờ ngủ của con lên. Mỗi ngày gọi con dậy\n      sớm 10 15f, đi ngủ sớm 10 15f, trong ngày thì vẫn khi nào mệt cho ngủ nap. Mọi thứ trong lsh đều dời sớm lên đến khi nào mẹ thấy giờ\n      dậy/ngủ của con hợp lý thì ngưng. Không nên gọi con dậy sớm hẳn 1 tiếng 2 tiếng, chỉ làm con mệt mà thôi.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('br')]),_vm._v(" "),_c('figure',{staticClass:"_2cuy _4nuy _2vxa"},[_c('div',{staticClass:"_h2x _h2y"},[_c('img',{staticClass:"_h2z _297z _usd img",attrs:{"src":"https://scontent.fdad3-1.fna.fbcdn.net/v/t1.0-9/120392490_659204085014742_9203292610866773901_n.jpg?_nc_cat=110&ccb=2&_nc_sid=32a93c&_nc_ohc=OgBOWLKqvX8AX82j2l