a là lần sau con đói ko dám hó hé nữa.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Nếu ói/sặc: ói ra sặc ra tính 1 lần, chờ l2 hoặc cữ sau nếu đã mời\n        2l\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("bú hết l1, pha thêm sữa mời l2. Bú hết l2, chờ cữ sau tăng sữa")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Nếu mẹ dùng ti giả nên cẩn thận, 1 số bé nghiện ti giả và bú ít hẳn.\n        1 mom có nhắc nhỏ mình là sau khi mom cai ti giả cho con thì con bú lên hẳn luôn. Cai vào ban ngày,\n        chỉ dùng khi ngủ ngày/đêm thôi\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Nếu đã dãn 4h thì sau này đừng quay lại 3h 3.5h nữa trừ khi con khóc ko\n        thể dỗ được bằng mọi cách (ngủ, tắm, ti giả, tv, phone, đi chơi v.v.)\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Phải xem bài 5 lsh gợi ý, áng chừng giờ ngủ của con tránh nhầm lẫn\n        gắt ăn và gắt ngủ. Bài 20 có ghi tín hiệu ngủ. Nếu mệt muốn ngủ thì cho ngủ dậy rồi ăn. Nếu\n        ngủ quá giờ ăn (quá 4h từ cữ trước) thì cho thêm 30f thôi, gọi dậy, tỉnh táo mời luôn.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Nếu sáng ra ko đói, ví dụ 7h dậy 8h mời, thì ngày của con bị ngắn mất\n        1 tiếng. Nếu mẹ dãn cữ 4h 1 lần, có khả năng con sẽ bị mệt vào cuối ngày và bỏ ăn bữa cuối.\n        Phải làm thế nào? Đây là lý do bài ghi trong vòng 3 tới 4 tiếng nếu đói thì cho bình. 1 ngày\n        của bé có khả năng sẽ như sau: 7h dậy, 8h mời bình, 11h30(hoặc 12h, 3.5h hoặc 4h) mời bình, 15h30\n        mời bình, 19h mời bình, đi ngủ. Thế là đủ 4 cữ 1 ngày trong 12 tiếng. Tuỳ theo nhu cầu mà làm.\n        Trong trường hợp khóc ko thể dỗ (TV, điện thoại, chơi, tắm, 