ại mình.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Mẹo cho mẹ: Trước khi tập, hãy đọc kỹ hiểu rõ bài ghim. Vì làm sai con\n      sẽ ăn kém và gặp nguy hiểm. Đăng bài lên nhóm hỏi nếu chưa hiểu rõ, đăng cập nhật nhờ hỗ trợ và\n      sửa sai nếu có.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Mẹo cho mẹ: Bàn bạc với người nhà. Hoặc là ôm con cả ngày. Vì có khá\n      nhiều mom đang tập cho con thì ông bà hoặc bố lén cho ăn. Nếu con khóc, mẹ có thể vờ như con đã ăn\n      rồi nhưng khó ở gắt khóc để người khác ko phá hỏng công sức mẹ tập con ăn trong nhiều ngày (cám\n      ơn mom nào đã góp ý nè, mình quên ko lưu lại nên ko nhớ tên).\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("RẤT QUAN TRỌNG: Bạn phải chắc chắn con bạn không có vấn đề sức khoẻ / bệnh tật gì. Mình biết vì con mình đã nằm viện, được khám bs, và khi mới sanh bé ăn rất ngoan. Bé chỉ biếng ăn sau khi bị mẹ ép ăn quá nhiều trong thời gian 6 tuần đầu. Trong thời gian mình áp dụng phương pháp này, bé đều được đi đến bs mỗi tuần để khám và theo dõi. Bé cũng có ống trong trường hợp bé không ăn gì hết mình vẫn có thể dùng ống cho bé ăn. Nếu bạn nào quyết định làm theo phương pháp này, xin hãy để ý việc tiểu tiện của bé, để bé không mất nước và gặp nguy hiểm. Nếu bé có bác sỹ hãy nói chuyện với BS của bé để tiện theo dõi khi cần thiết. ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Cần theo dõi tránh bé ăn quá ít (dưới 300ml trong nhiều ngày). Bé có khả năng hạ đường huyết, mệt mỏi, ngủ li bì, ỉu xìu ko năng động, đói lả. Nếu có dấu hiệu này mẹ nên cho uống nước ngọt có đường, nước cam, kẹo, ăn viên đươ