l',{staticClass:"_5a_q _509r",attrs:{"dir":"ltr"}},[_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Bé mệt. Bé đói và muốn ăn nhưng bé cũng mệt và muốn ngủ, nên bé\n            bị rối loạn và ko rõ mình nên làm gì trước. Bạn phải để ý giấc ngủ của bé. Bé ngủ đủ\n            giấc sẽ ăn tốt và ngoan hơn. "),_c('span',{staticClass:"_4yxo _4yxp"},[_vm._v("Nếu giờ ăn trùng giở ngủ, cho bé ngủ dậy rồi ăn sau. Cần làm mọi cách ru bé ngủ, thiếu ngủ sẽ ăn kém.")])]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Bé bệnh/bị đau: bé bị đau sẽ khó chịu, bé muốn ăn nhưng việc ăn\n            làm bé đau. Bạn cần chắc chắn bé khoẻ mạnh ko bệnh tật trước khi bắt đầu\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Núm bị bẹt lại: một số bé mút mạnh làm núm bị bẹt lại và bé\n            không mút được, mẹ nên xem xét cỡ núm vừa tuổi bé.\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Núm quá to và sữa chảy quá nhanh sẽ làm bé sợ, cho nằm nghiêng nếu\n            mẹ vừa tăng cỡ núm, sữa chảy nhanh sẽ tràn ra mép\n          ")])])]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Bé đang ăn thì ngưng lại 1 cách bình tĩnh: nếu bé ko tỏ thái độ khó\n        chịu gay gắt, nếu bé ngưng lại và nhìn ngó xung quanh vì tò mò (rất bình thường), bạn có thể\n        đưa bình vào tầm mắt bé để bé nhìn thấy và xem bé còn muốn ăn tiếp ko. Nếu bé từ chối hãy\n        dừng bữa ăn và cất bình. Không bao giờ ngọ ngoạy núm trong mồm bé, không gõ bình, không ghì\n        bình trong mồm, không làm bé khó chịu và vô tình gây áp lực ăn cho bé.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Một số bé cầm bình và rút ra rồi lại bỏ vào miệng nhiều lần, nhai,\n        cắn, lấy l