i bé\n        có thói quen quơ quơ tay, hay vô tình gạt đổ bình, cho bé cầm 1 món đồ chơi nhỏ sẽ giúp bé ko\n        quơ tay nữa. Mẹ không được cầm đồ chơi rung lắc làm con mất tập trung. Mẹ có thể nói chuyện với\n        con giúp con thả lỏng tâm tình, nhưng không nên hát hò chọc ghẹo làm bé mất tập trung. (quơ tay\n        khác với dùng tay rút/hất bình ra khỏi miệng)\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Khi bé vẫn còn mút hãy để bé tự dừng lại, đừng rút bình ra để vỗ\n        ợ, lau mặt, ..\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Hãy giữ bé ngủ điều độ, thức dậy cùng 1 giờ mỗi ngày để đồng hồ\n        sinh học của bé ổn định giấc ăn/ngủ\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Khi bắt đầu, chỉ nên để 1 người cho bé ăn (mẹ), tối đa 2 người. Sau khi\n        bé đã không còn dấu hiệu sợ hãi, bố hoặc bà có thể cho bé ăn. Tất cả mọi người đều phải\n        biết cách mời bé ăn và không được cố ép bé ăn hết bình. Chỉ cần 1 lần, bé sẽ dễ dàng sợ ăn\n        trở lại.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Không bỏ mùi vị lạ vào sữa bé trong lúc thực hiện phương pháp này và\n        bé vẫn chưa hết sợ ăn. Không nên cho bình lúc thì sữa mẹ lúc thì sữa bột. Tốt nhất trong quá\n        trình tập chỉ nên có 1 loại sữa.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Vài bé cảm nhận nhiệt độ tốt và muốn sữa ở 1 nhiệt độ nhất định.\n        Nếu hâm sữa thì hãy hâm sữa cả 2 lần mời. Mình để sữa ở nhiệt độ phòng vì ko cần lo lắng về\n        nhiệt độ khác nhau khi hâm sữa\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Hãy dùng 1 loại bình, không thay đổi nhiều loại bình khi thực hiện\n        phương pháp. Mỗi loại bình có núm và cách mút khác nhau, thay đổi nhiều làm loạn bé.\n      ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s"},[_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("C"),_c('span',{staticClass:"_4yxo _4yxp"},[_vm._v("hấm điểm thái độ trong bữa ăn của bé")]),_vm._v(".\n          (mình dịch từ tiếng anh, ngôn từ không chau truốt lắm)\n        ")]),_vm._v(" "),_c('ol',{staticClass:"_5a_q _509r",attrs:{"dir":"ltr"}},[_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("1 điểm: bé khóc thét giãy giụa không muốn được bế cho ăn hoặc khi\n            bạn cho bé nhìn bình sữa bé lập tức khóc, ưỡn người, gắt gỏng từ chối\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("2 điểm: thái độ bé ko rõ ràng, bé khóc quấy trong bữa ăn, đang mút\n            thì đột ngột dừng lại và tâm trạng bé có vẻ khó chịu\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("3 điểm: bé có vẻ bồn chồn không yên khi ăn, bé dừng mút đột ngột\n            và có thái độ căng thẳng\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("4 điểm: bé hơi hơi khó chịu khi mút, đôi lúc bé mất tập trung khi\n            mút, bé dừng bữa ăn 1 cách bình tĩnh\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("5 điểm: bé ăn/mút với tâm trạng thoải mái, bé dừng bữa ăn 1 cách\n            bình tĩnh\n          ")]),_vm._v(" "),_c('li',{staticClass:"_2cuy _509s _2vxa"},[_vm._v("Nếu bạn cho bé ăn, và điểm của bé mỗi bữa ăn là 4 hoặc 5, thì bạn\n            đã thành công chữa bệnh sợ ăn cho bé nhé. Nên nhớ thái độ ăn của bé được cải thiện trước,\n            sau đó mới đến lượng sữa. Bé có thể sụt cân trong quá trình, và bé sẽ ăn nhiều lên từ từ\n            gỡ lại cân đã mất.\n          ")]),_