ẹ đút ko ăn, chỉ ăn khi bé có đồ chơi tv, phone, được bế đi rong, lơ mơ ngủ, ăn trong bóng tối, có\n        nhạc hoặc tiếng ồn trắng làm bé mất tự chủ\n      ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Trên đây là những dấu hiệu của bé bị biếng ăn tâm lý (BATL). Bé có thể bị BATL vì nhiều lý do. Nhưng lý do\n      quan trọng nhất là bé bị sợ hãi căng thẳng mỗi khi đến giờ ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Một trong những sai lầm lớn nhất và cũng là lý do phổ biến nhất, đó là lịch sinh hoạt chưa hợp lý. Các\n      bé ít tháng chỉ bú sữa, sẽ bú theo nhu cầu. Rất nhiều mẹ cho con ăn theo giờ, đây là lỗi sai phổ biến. Có những lúc bé chưa đói,\n      mẹ đã đúng giờ cho ăn, đây là 1 kiểu ép ăn mà ít ai nhận ra. Những tháng đầu đời bé có phản xạ bú, mẹ cho ti là lập tức mút. Sau\n      3 tháng phản xạ bú đã không còn, bé sẽ không mút, mẹ xót con không ăn, ép con, dẫn đến biếng ăn tâm lý. Đối với ăn dặm, nhất là ăn\n      dặm truyền thống, 2 tiếng ăn dặm, 2 tiếng ăn sữa. Cách ăn này không hợp lý, vì dạ dày bé phải hoạt động liên tục không được nghỉ ngơi.\n      Bé ăn cả ngày như ăn vặt, lúc nào bụng cũng lưng lửng, thành ra không no không đói cả ngày. Đến bữa không đói không thèm ăn, không hợp\n      tác, hoặc ăn ít, bị mẹ ép ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Một sai lầm phổ biến khác, đó là lối suy nghĩ ăn hết 1 bát cháo, bú hết bình mới đủ no. Bé là người,\n      không phải máy móc, người thì có lúc ăn nhiều ăn ít, tuỳ tâm trạng, tuỳ vận động nhiều hay ngủ nhiều v.v. Mỗi cữ bé ăn sẽ không\n      giống nhau, mẹ chỉ nên mời, con ăn bao nhiêu hãy tuỳ con. Con sẽ không để bụng đói đi ngủ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Một sai lầm khác, đó là dụ con chơi TV, điện thoại, đồ chơi, cố gắng nhét thêm 1 2 thìa cho con ăn. Đây là 1\n      dạng ép ăn. Khi có đồ chơi, bé không tự chủ được bản thân, ăn khi không tập trung, bé sẽ không biết ăn khi nào thì đã đủ no đến cữ\n      sau. Bạn cần để bé tự chủ và tự giác khi ăn, thấy thức ăn và nhận biết mình đang ăn. Nếu bạn đưa thức ăn và bé hợp tác tốt, đó\n      là bé vẫn còn đói. Nếu bé không hợp tác, nên dừng bữa ăn ngay lập tức.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Một lý do BATL khác, là do sinh lý hoặc có thay đổi lớn trong môi trường và nếp sinh hoạt của bé. Đến những thời\n      kỳ WW (ngủ kém, mệt nên ăn kém hơn, WW không ảnh hưởng ăn uống) hoặc biếng ăn sinh lý do bệnh tật, mọc răng, thậm chí kể cả khi du lịch, chuyển\n      nhà, có anh chị em, người lạ v.v., bé sẽ ăn ít hơn. Đây là điều bình thường, mẹ hãy kệ bé, khi qua đốt biếng ăn sinh lý, bé sẽ ăn lại\n      như cũ, cũng có khả năng ăn bù cho đợt biếng ăn sinh lý. Nếu mẹ ép ăn trong giai đoạn này, sẽ dẫn đến BATL, đói cũng không ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Một lý do BATL khác, đó là bé có bệnh nền. Ví dụ bệnh dị ứng sữa bò hoặc trào ngược, mỗi lần ăn gây\n      đau đớn nên bé đâm ra sợ hãi. Mẹ nên mang bé đi bác sỹ nếu có dấu hiệu biếng ăn, bất thình lình bỏ ăn. Không ép con vì bé sẽ sợ\n      hãi và mẹ cũng không biết bé có bệnh nền gì dẫn đến biếng ăn bỏ ăn bất chợt. Nếu bệnh nền không được giải quyết thì bé sẽ sợ ăn\n      lâu dài. Chỉ khi bệnh nền đã được giải quyết mới có thể chữa cho bé theo phương pháp của bài 21/22.\n    ")])])])}]

