1 tiếng sau khi pha - k để được lâu như sữa mẹ. Lí do: vi khuẩn sinh sôi trong sữa công thức\n        nhanh hơn sữa mẹ vì trong sữa công thức k có kháng thể.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Nếu mẹ ít sữa mẹ có thể thử cách kích sữa sau:")])]),_vm._v(" "),_c('ul',{staticClass:"_5a_q _5yj1",attrs:{"dir":"ltr"}},[_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Ăn các loại ngũ cốc lợi sữa. Mình biết yến mạch lợi sữa vì bản thân đã dùng. Mẹ nấu nhừ khoảng 20f,\n        loãng như cháo, nêm thêm rau củ thịt và ăn hàng ngày. Uống nhiều nước vì sữa vốn là nước. Mỗi khi con bú/hút sữa mà ti đã sạch\n        sữa, mẹ hút thêm 10f – 20f cho sữa về thêm. Càng hút nhiều thì sữa càng về nhiều. Cần chườm nóng, matxa, xoa bóp ngực khi hút sữa\n        như bị tắc tia sữa.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Nếu mẹ tập bình, con ko bú mà bị tắc tia sữa:")])]),_vm._v(" "),_c('ul',{staticClass:"_5a_q _5yj1",attrs:{"dir":"ltr"}},[_c('li',{staticClass:"_2cuy _509q _2vxa"},[_vm._v("Mẹ có thể thử lấy 1 bình nước nhỏ vừa tay, đổ nước nóng vào, quấn khăn sữa để không bị bỏng. Trước khi\n        hút sữa mẹ dùng bình nước lăn quanh ngực, đặc biệt là bên dưới bầu ngực (vì sữa đọng ở đây) và xung quanh 2 bên khoảng 5-10f. Khi\n        hút sữa mẹ dùng tay nâng bầu ngực, xoa bóp, lần lượt ép 2 bên ngực. Nếu sau khi hút sữa vài phút và sữa bắt đầu chảy chậm lại\n        mà ngực vẫn còn nặng, còn sữa, mẹ tiếp tục lăn bình nước nóng, mát xa ngực khi hút sữa. Khi nào ngực nhẹ tênh và hoàn toàn ko\n        còn sữa hoặc khối u (hạch sữa) là được.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Cách tiết kiệm sữa mẹ: ")]),_vm._v("Nếu bình 1 bé bú dư 20, bình 2 bé bú dư 50, bình 3 bé bú\n      dư 10, mẹ có thể gộp chung lại và cho vào bình cuối cùng của ngày là được 80ml. Sữa này được hâm lại thêm 1 lần trong tối đa 24h.\n      Sau 24h phải đổ bỏ. (Con số 24h ko chính xác lắm, có nguồn bảo chỉ được dùng lại sữa mẹ sau tối đa 4h, mẹ nên để ý nhiệt độ\n      phòng, nhiệt độ tủ lạnh, thử sữa trước khi cho bé bú). Nguồn từ CDC ghi 2h ở nhiệt độ phòng. Mom nào muốn thử cách này thì nên\n      cẩn thận với nhiệt độ bảo quản, tay chân phải sạch sẽ khi chạm vào sữa, bình phải tiệt trùng. Mình biết có người vẫn để 24h sau\n      hâm lại và chả bị gì cả. "),_c('span',{staticClass:"_4yxo"},[_vm._v("Nhưng nếu sợ thì tốt nhất ko thử!")]),_vm._v(" Có thể pha sm và sct chung theo cùng 1 tỉ lệ\n      thì bé vẫn bú mà ko sợ thiếu sữa mẹ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Sữa trữ đông có mùi là do enzyme lipase,")]),_vm._v(" enzyme này phân huỷ chất béo trong sữa mẹ\n      tạo mùi hắc, có khi như xà phòng. Để tránh làm sữa có mùi lạ mẹ có thể thử phương pháp sau: Dùng 1 nồi nhỏ, dành riêng cho việc\n      hâm sữa trước khi trữ đông (tránh nhiễm khuẩn sữa mẹ nếu dùng chung với việc nấu ăn). Mẹ có thể hâm sữa đến khi có bọt nhỏ bong\n      bong nổi lên quanh thành nồi nhưng ko được nấu đến khi sữa sôi sục (nấu sôi sẽ hỏng hết chất dinh dưỡng/kháng thể). Nhiệt độ vào\n      khoảng 180F hoặc 80C. Việc hâm sữa luôn luôn làm mất 1 ít chất dinh dưỡng trong sữa mẹ. Mẹ nên thử trữ đông và rã đông 1 bịch sữa nhỏ\n      xem có mùi vị lạ hay ko? Nếu có thì nên sử dụng cách này để tránh làm sữa có mùi khó chịu, con bỏ bú. Mẹ cũng có thể thử pha 1\n      ít sữa rã đông với sữa mới giảm bớt mùi hắc thì bé có thể sẽ chịu bú. (Kellymom.com and medela.com)\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Nếu cất sữa chung với tủ lạnh gia đình, mẹ cần lau chùi tay cầm và cửa tủ lạnh. Sữa nên để trên cùng, tránh xa thịt, rau củ, thức ăn chín. Cần tiệt trùng bình, rửa tay trước khi chạm vào sữa. Vi khuẩn từ thức ăn rất nguy hiểm nếu dính lẫn vào sữa.")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Rã đông sữa mẹ:")]),_vm._v(" nên rã đông sữa trong tủ lạnh qua đêm, rã đông trong 1 ly nước ấm,\n      hoặc rã đông dưới vòi nước ấm đang chảy. Không hâm sữa trong lò vi sóng, sẽ hỏng chất dinh dưỡng, có thể làm bỏng mồm con. Sữa rã\n      đông hoàn toàn có thể dùng trong 24h tính từ lúc đã rã đông hết chứ ko tính từ lúc lấy ra khỏi tủ trữ đông. Nên rã đông sữa cũ\n      nhất trước vì thời gian càng lâu sữa càng mất chất. Nên ghi rõ ngày giờ hút/trữ đông.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Nếu bịch sữa để trong tủ lạnh có váng mỡ nổi lên, sau khi hâm mẹ lắc lắc bịch sữa cho mỡ hoà tan vào\n      sữa.\n    ")]),_vm._v(" "),_c('figure',{staticClass:"_2cuy _4nuy _2vxa"},[_c('div',{staticClass:"_h2x"},[_c('img',{staticClass:"_h2z _297z _usd img",attrs:{"src":"https://scontent.fdad3-3.fna.fbcdn.net/v/t1.0-9/119747502_652125342389283_9135533810946396969_n.jpg?_nc_cat=100&ccb=2&_nc_sid=32a93c&_nc_ohc=BzmC7sZzNFAAX89yBUv&_nc_ht=scontent.fdad3-3.fna&oh=435b3aa67b0bd6a600b83f082b83393f&oe=6011F283","alt":"","id":"u_0_3"}})])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Lưu ý nhiệt độ phòng ở đây là 25C. Nhà nóng thì thời hạn sử dụng sữa sẽ ngắn hơn.")])])])}]

