. Nếu con ăn ít\n      hơn 300ml/ngày, mẹ nên chờ tối đa 4h từ cữ trước, con ngủ ko dậy mẹ có thể lén cho ăn để đủ 300ml. Luôn luôn mời bình trước, nếu ngủ\n      mà ko ăn bình thì cho ti mẹ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Nếu ngủ, mời 2 lần rồi, ăn bình rồi, mà vẫn ko đủ 300ml, mẹ chờ cữ kế tiếp (3-4h sau), con tỉnh thì mời\n      bình, con ko tỉnh dậy thì lén cho ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("15. Tại sao mời 2 lần")]),_vm._v("? Vì mẹ có 2 ti "),_c('span',{staticClass:"_47e3 _5mfr",attrs:{"title":"Biểu tượng cảm xúc wink"}},[_c('img',{staticClass:"img",attrs:{"height":"18","role":"presentation","src":"https://static.xx.fbcdn.net/images/emoji.php/v9/t78/2/18/1f609.png","width":"18","alt":""}}),_c('span',{staticClass:"_7oe",attrs:{"aria-hidden":"true"}},[_vm._v(";)")])]),_vm._v(". Ti mẹ hơi ít sữa, con bú 1 bên chưa đã thèm, mẹ vỗ ợ xong mời bên còn lại. Nếu ti mẹ nhiều sữa, con bú no 1 bên rồi,\n      mời bên còn lại con từ chối, hoặc ăn lấy lệ ăn ít, bỏ luôn, cho bú 1 bên thôi (tức là mời 1 lần thôi), cữ sau cho bú bên kia trước.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("16. Tại sao cai dặm cai ti mẹ? ")]),_vm._v("Vì có lựa chọn tốt hơn thì ăn bình làm gì?")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("17. Tại sao 1 loại sữa 1 loại bình 1 nhiệt độ")]),_vm._v("? Các bé tinh ý, ko thích sự thay đổi,\n      sữa mùi khác ko thích, nhiệt độ khác ko thích. Mẹ bắt đầu thì phải loại trừ tất cả khả năng con có thể bỏ ăn. Bình thì mỗi bình\n      1 cách mút khác nhau, nên chọn 1 loại thôi. Núm phải đúng, núm sai, to quá hoặc bé quá con đều ko ăn được. Ăn ngoan rồi thì có thể\n      đổi bình đổi sữa v.v., con có thể bỏ 1 2 bữa khi mẹ đổi nhưng sẽ ăn ngoan lại sau đó.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("18. Tại sao giờ ăn trùng giờ ngủ thì cho ngủ trước")]),_vm._v("? Vì mệt ăn kém. Mệt vs đói thì\n      mệt thắng, con sẽ chọn ngủ. Có mời cũng ko ăn hoặc ăn ít. Phí 1 lần mời.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("19. Có nên dreamfeed ko")]),_vm._v("? Mình ko ủng hộ dreamfeed trừ khi con ăn quá ít, ít hơn 300ml\n      (khoảng 200ml đổ xuống dễ lả lắm), ngủ ko dậy, thì đây là ngủ li bì, dấu hiệu của đói lả. Phải lén cho ăn, bình ko ăn thì cho ti\n      mẹ. Nếu mời cữ này ko ăn, thì mẹ chờ con tự dậy hoặc chờ cữ sau. Thường nếu mẹ làm đúng, con phải ăn khoảng 300ml.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("20. Dreamfeed ảnh hường gì? ")]),_vm._v("Ảnh hưởng đến sức phán đoán của con về lượng thức ăn\n      con cần. Khi bú ngủ, khả năng là con chưa đói (leptin>ghrelin, con còn năng lượng dự trữ) mẹ có mời con cũng ăn ít hoặc ko ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("21. Tại sao 300ml?")]),_vm._v(" Liên quan đến hạ đường huyết. Khi đường huyết tụt xuống 1 mức nào\n      đó, thì cơ thể tiết ra ghrelin làm người cảm thấy đói. Tụt đến 1 mức báo động, thì sẽ tự tìm thức ăn để bảo vệ bản thân khỏi\n      nguy hiểm. 300ml là con số tối thiểu để cơ thể hoạt động, các bé ko bị ép nữa, sẽ tự ăn đủ 300ml. Nếu mẹ làm sai, vô tình ép con,\n      thì con sẽ ăn ít hơn 300ml trong ngày đầu. Ko sửa sai thì sẽ ăn ít hoài luôn. Nguy hiểm tính mạng.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("22. Tại sao đói, cho bình lại ăn ít")]),_vm._v("? Vì khi sợ ăn, căng thẳng kéo dài, cơ thể có\n      phản ứng chiến hay chạy, phản ứng này làm hệ tiêu hoá ngưng hoạt động. Thành ra ăn có vài ml là nhả ko ăn nữa vì nhu động dạ dày\n      hoàn toàn ngừng lại rồi.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("23. Sợ ăn kéo dài nguy hiểm đến đâu")]),_vm._v("? Cơ thể từ chối chấp nhận thức ăn. Sợ hãi làm\n      cho con ăn ít mà vẫn ói ra. Mẹ thay vì dãn cữ đúng độ tuổi lại chia nhỏ bữa ăn vì lầm tưởng con bị trào ngược, dạ dày con nhỏ ko\n      ăn được lượng lớn thức ăn. Mời quá nhiều cữ, mời quá nhiều lần, kéo dài sợ hãi ko bao giờ hết.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("24. Tại sao bữa sáng lại ăn ít/bỏ ă")]),_vm._v("n? Vì khi ngủ cơ thể tiết ra leptin, làm giảm\n      đói và ngủ ngon. Bé sợ ăn, sáng ra chất này vẫn còn tồn tại, con ko thấy đói, ko ăn, ăn ít. Đây cũng là lý do sáng dậy cần chờ\n      khoảng 1 tiếng, khi nào đói mới cho ăn để con liên hệ việc đói = có bình sữa.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("25. Khi nào hết sợ ăn")]),_vm._v("? Bài 2 có ghi bảng chấm điểm thái độ ăn, khi tất cả các bữa\n      ăn con đều được 4 hoặc 5 điểm trong 3 hôm thì con hết sợ ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("26. Khi nào ăn nhiều lên")]),_vm._v("? Sau khoảng 1-2 tuần, thái độ ăn tốt con sẽ tự tăng lượng\n      sữa lên.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("27. Khi hết sợ ăn")]),_vm._v(": Bữa sáng và tối ăn nhiều nhất trong ngày. Sáng ăn nhiều vì ngủ\n      cả đêm đói. Tối ăn nhiều vì để tích trữ ngủ xuyên đêm. Khi hết sợ ăn, buổi sáng con dậy là sẽ đòi ăn ngay và luôn. Lý giải cho việc\n      này là vì trong quá trình tập ăn, giờ ngủ trùng giờ ăn thì mẹ luôn luôn để con ngủ khi con mệt, đói sẽ dậy ăn. Ban đêm ko cho\n      dreamfeed bú ngủ, mà khi con ngủ đêm cảm thấy đói bụng, con dậy khóc đòi ăn mẹ mới cho ăn. Mẹ ko nên nhìn vào số lượng sữa mỗi bữa.\n      Tổng sữa 1 ngày ko xê dịch nhiều tức là con đã ăn ổn định rồi.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("28. Khi nào nên thêm sữa vào bình")]),_vm._v(": Khi mà cữ này con luôn luôn bú sạch bình, vẫn còn\n      thòm thèm mút bình không, thì sau 2 3 hôm mẹ nên tăng sữa trong bình, tăng sớm quá hay tự tiện tăng chỉ phí sữa mà thôi.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("29. Ăn bao nhiêu là đ")]),_vm._v("ủ? Ông hàng xóm ăn 10 bát cơm, mẹ có ăn 10 bát ko? Con muốn ăn\n      nhiêu kệ con.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("30. Biếng ăn sinh lý là sao")]),_vm._v("? Con có thái độ ăn tốt nhưng ăn rất ít. Nên đem đi khám\n      bác sỹ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("31. Giai đoạn tăng trưởng nhanh:")]),_vm._v(" giống như dậy thì vậy. Con có thể khó ở, khó ngủ,\n      ăn nhiều, ngủ nhiều, nói chung là loạn xạ ngậu do hormone. Nếu trong thời gian này, có khi nào con đã ăn rồi. Nhưng 1 hoặc 2 tiếng sau\n      lại khóc không thể dỗ được bằng mọi cách (tv, iphone, ca nhạc, tắm, ngủ, ti giả v.v.) thì cho ăn luôn. Đây là cluster feed. Ăn cữ dồn\n      dập vì đói, cần thêm năng lượng để tăng trưởng. Giai đoạn này con sẽ lớn khá nhanh. Bài cũng có ghi, khi khóc ko thể dỗ được, cho ăn\n      bất kỳ lúc nào dù chưa đến đúng cữ. Nhưng đừng nhầm với gắt ngủ nhé. xem bài 20 tín hiệu ngủ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("32. Sơ bộ về trào ngược:")]),_vm._v(" nó là ợ nóng. Ít tháng thì cơ ngăn giữa thực quản và dạ\n      dày chưa hoàn chỉnh, bé bú xong ợ sữa lẫn acid lên họng, acid làm cháy thực quản và họng, mỗi lần ăn là mỗi lần bị đau nên con bỏ\n      ăn, sợ ăn. Bệnh này cần có thuốc, nếu ko thì ko chữa được chứng sợ ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("33. Trào ngược im lặng")]),_vm._v(": con ợ lên và nuốt xuống ngay, miệng hay nhai nhai cặn sữa. Sau\n      khi con ăn mẹ có thể nghe con nuốt ực ực mà ko hề thấy gì trong mồm con (ợ sữa lên miệng xong lại nuốt ngay lập tức nên nghe ực ực)\n    ")])])])}]

