 sáng. Hợp tác tốt cả 2 cữ dặm thì cho 3 cữ. Không nên cho cả 3 cữ dặm ngay từ đầu.\n      Cách đút cho bé có thể tìm trong bài ghim 17. Xem thêm hướng dẫn trong bài 22\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Lsh 7-"),_c('span',{staticClass:"_4yxp"},[_vm._v("10th")])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("4 cữ chính")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Sáng trưa có dặm phụ. Sau sữa 20f (hoặc ngắn hơn tuỳ sức bé, nếu bé có thể ăn dặm sau 10f, bạn có thể chờ\n      10f). Dặm phụ 1 thìa 2 thìa cũng là dặm. Tập nuốt ăn thô là chủ yếu. Cho bé ăn để biết đây là giờ ăn, sau này đảo lại dặm lên trước. Nếu bé\n      nào thích ăn dặm sẽ ăn ít sữa để bụng ăn dặm, và có khả năng tự cai sữa.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Chiều có dặm chính, cách 3h30 từ cữ sữa trước. Đủ đói để hợp tác. 7h ăn sữa đi ngủ.")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Cai đêm mới ăn nhiều được. Đêm dậy thì dỗ ngủ lại. Ko ngủ, khóc hoài ko nín thì cho ăn. Cai từ từ bằng giãn cữ hoặc giảm\n      lượng sữa từ từ.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Ăn dồn 4 cữ để ăn thật no. Bữa sau thật đói. Đói mới có nhu cầu ăn và hợp tác tốt. Ko ăn kiểu adtt là 2 tiếng sữa. 2 tiếng\n      sau lại dặm. Lắt nhắt như ăn vặt thì bụng lưng lửng ko no ko đói cả ngày. Không đói thì tất nhiên là sẽ ko hợp tác ăn. Bé nào ăn nhiều sữa\n      sẽ ăn ít cháo và ngược lại. Sức ăn 1 ngày chỉ đến thế thôi, không thể nhiều sữa và cháo được. Mẹ nên nhìn tổng thể 1 ngày chứ\n      không nên nhìn từng bữa ăn. Bữa trước ăn nhiều quá thì bữa sau sẽ đói trễ hơn và ăn ít lại.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Luật mời ăn: sữa cho 2 cơ hội. Mời 1 lần ko ăn cất bình ko cho nhìn nữa (quan trọng, ko cho bé nhìn). Chờ 5-20f. Mời lần 2.\n      Ko ăn cất bình. Dặm 1 cơ hội. Ko ăn cất bát đi. Bế ra ngoài ghế. Không ép con ăn, không dụ đồ chơi tv bế đi rong, con mất tập trung mẹ đút, ko\n      tự chủ được no hay đói, sẽ dẫn đến biếng ăn tâm lý. Mẹ đút phải xem con có há miệng chờ không, nếu mím môi quay đi tức đã no và\n      không muốn ăn.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Sau 12 tháng thì đảo lại dặm trước sữa sau. Ra 3 cữ sáng trưa tối. Kèm 1 cữ xế. Mẹ phải nhất quán. Ko ăn là bị đói. Con sẽ\n      ăn ngoan cho gì ăn nấy.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_c('span',{staticClass:"_4yxo"},[_vm._v("Lịch sinh hoạt ")]),_vm._v("1"),_c('span',{staticClass:"_4yxo"},[_vm._v("2-18th")])]),_vm._v(" "),_c('figure',{staticClass:"_2cuy _4nuy _2vxa"},[_c('div',{staticClass:"_h2x"},[_c('img',{staticClass:"_h2z _297z _usd img",attrs:{"src":"https://scontent.fdad3-2.fna.fbcdn.net/v/t1.0-9/118186913_633160637619087_2256517279427215579_n.jpg?_nc_cat=107&cb=846ca55b-311e05c7&ccb=2&_nc_sid=32a93c&_nc_ohc=m5lptdgaVYMAX8qh6AJ&_nc_ht=scontent.fdad3-2.fna&oh=af53bf2134aa447da689fbb907e1b9b3&oe=6010FAA3","alt":"","id":"u_0_5"}})])]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("lsh 12-18th: Nếu sáng dậy, 1 vài bé quá đói và ko chịu ăn dặm, mẹ vẫn có thể cho sữa trước, nhưng giảm sữa lại để bé tập\n      trung ăn dặm.\n    ")]),_vm._v(" "),_c('div',{staticClass:"_2cuy _3dgx _2vxa"},[_vm._v("Giờ ngủ trong bài là tuỳ bé. Bé nào lớn chỉ ngủ 1 giấc dài chứ ko ngủ thành 2 giấc nữa.")])])])}]

