<template>
  <div>
    <div class="_39k5 _5s6c">
      <div class="_2cuy _3dgx _2vxa">Theo dõi kỹ con, nếu có dấu hiệu nguy hiểm, nếu ăn quá ít, ngủ li bì, hoặc ăn ít
        nhiều ngày liền mà con ngủ nhiều mệt mỏi mất nước v.v. lo lắng thì nên đem con đi bệnh viện, đừng chần chờ gì
        hết, có bé gặp nguy hiểm và lả rồi.
      </div>
      <div class="_2cuy _3dgx _2vxa">Nếu ăn quá ít, ít hơn 300ml nhiều, hoặc ăn tầm 300ml trong 5 ngày, tức là mẹ làm
        sai đấy, cần có video/cập nhật hợp lệ theo bài 21 (trên note, ko trên app, kèm thái độ ăn của con). Mẹ cần phải
        ép con ăn theo kiểu cũ cho đủ 300ml, hoặc ngưng tập 1 2 ngày, đọc kỹ lại bài ghim xem sai sót ở đâu và tập lại
        nhé.
      </div>
      <ul class="_5a_q _5yj1" dir="ltr">
        <li class="_2cuy _509q _2vxa">xem bài 6 chọn cỡ núm đúng đi đã</li>
        <li class="_2cuy _509q _2vxa">24h tính từ 0 dến 24h</li>
        <li class="_2cuy _509q _2vxa">cữ kế tính từ lúc bắt đầu mời Lần 1 của cữ trước</li>
        <li class="_2cuy _509q _2vxa">Dưới 3th, không có lịch sinh hoạt, rèn ăn là chính, mệt thì cho ngủ, không kéo
          thời gian thức, hỗ trợ con ngủ nếu chưa tự ngủ được, giữ giờ ngủ ổn định để con không mệt. Dưới 3th, không
          thức được 1 tiếng vào sáng sớm thì chờ 30f 45f. Thức được thì chờ 1 tiếng.
        </li>
        <li class="_2cuy _509q _2vxa">Từ 1th trở lên: dãn tối thiểu 3h, con đòi ăn nhiệt tình thì cho ăn, không thì chờ
          4h hãy cho.
        </li>
        <li class="_2cuy _509q _2vxa">Từ 3th trở lên: dãn tối thiểu 3.5h, nếu không đòi ăn nhiệt tình, chờ 4h hãy cho.
        </li>
        <li class="_2cuy _509q _2vxa">từ 6 - 7th trở lên: dãn hẳn 4h đi, trừ khi khóc quá, thì cố gắng dãn 3h.</li>
        <li class="_2cuy _509q _2vxa">Bất kỳ khi nào khóc không dỗ được (bằng mọi cách, dỗ ngủ, tắm, đi chơi, tv, phone
          …), cho ăn luôn.
        </li>
        <li class="_2cuy _509q _2vxa">Dỗ ngủ bằng mọi cách (có thể cho ti giả khi ru ngủ ngày/đêm) vì mệt sẽ ăn kém.
        </li>
        <li class="_2cuy _509q _2vxa">Ban đêm nếu con khóc/tỉnh dậy đòi ăn, mời 1l mà bé đang bú rồi ngủ
          mất, thì ko nên mời l2 nữa, vì bé ngủ mà cho ăn là bú ngủ rồi. Chỉ nên cho bú ngủ nếu con ăn
          ít hơn 300ml
        </li>
        <li class="_2cuy _509q _2vxa">chủ đề Clip ép ăn sữa (nên xem, tránh mắc lỗi ép ăn mà ko biết): Khi nào
          mẹ nên ngưng cho ăn: nhìn bình là giãy lên, quay đi ko nhìn bình, giấu mặt, khóc, ưỡn người, đang
          bú mà con nhai núm, cắn núm, ko mút mà chỉ ngậm hoặc mút nhẹ hều, lưỡi đảo núm, đẩy núm, tay
          đập bình, quơ tay hất bình, gạt bình, lẫy, lăn đi, lắc đầu, quay đi chỗ khác (dù vẫn mút nhưng
          quay đi mẹ nên ngưng), mút nhả mút nhả nhiều lần, mút nhả mà kéo bình vô chỉ mút có 5 10ml rồi
          lại nhè. Mẹ cũng cần kiểm tra núm có đúng cỡ ko - bài 6. CON MÀ KO MÚT, LÀM GÌ MẸ KO RÕ
          RÀNG, CỨ VIỆC CẤT BÌNH.
        </li>
        <li class="_2cuy _509q _2vxa">Đang mút hả miệng cười, cất bình lần sau đói sẽ chừa.</li>
        <li class="_2cuy _509q _2vxa">Đang mút quay đi (hoặc tự kéo bình ra vào), ko dí bình theo chờ con quay
          lại, nếu quay lại mà mút đểu giả (thì tính là bé từ chối, quay lại là lần 2 rồi, ko mời
          nữa). Nếu quay lại mà mút nhanh mạnh thì tính vẫn là lần 1. Tốt nhất là cứ cất bình luôn nếu
          nhả ra là lần sau con đói ko dám hó hé nữa.
        </li>
        <li class="_2cuy _509q _2vxa">Nếu ói/sặc: ói ra sặc ra tính 1 lần, chờ l2 hoặc cữ sau nếu đã mời
          2l
        </li>
        <li class="_2cuy _509q _2vxa">bú hết l1, pha thêm sữa mời l2. Bú hết l2, chờ cữ sau tăng sữa</li>
        <li class="_2cuy _509q _2vxa">Nếu mẹ dùng ti giả nên cẩn thận, 1 số bé nghiện ti giả và bú ít hẳn.
          1 mom có nhắc nhỏ mình là sau khi mom cai ti giả cho con thì con bú lên hẳn luôn. Cai vào ban ngày,
          chỉ dùng khi ngủ ngày/đêm thôi
        </li>
        <li class="_2cuy _509q _2vxa">Nếu đã dãn 4h thì sau này đừng quay lại 3h 3.5h nữa trừ khi con khóc ko
          thể dỗ được bằng mọi cách (ngủ, tắm, ti giả, tv, phone, đi chơi v.v.)
        </li>
        <li class="_2cuy _509q _2vxa">Phải xem bài 5 lsh gợi ý, áng chừng giờ ngủ của con tránh nhầm lẫn
          gắt ăn và gắt ngủ. Bài 20 có ghi tín hiệu ngủ. Nếu mệt muốn ngủ thì cho ngủ dậy rồi ăn. Nếu
          ngủ quá giờ ăn (quá 4h từ cữ trước) thì cho thêm 30f thôi, gọi dậy, tỉnh táo mời luôn.
        </li>
        <li class="_2cuy _509q _2vxa">Nếu sáng ra ko đói, ví dụ 7h dậy 8h mời, thì ngày của con bị ngắn mất
          1 tiếng. Nếu mẹ dãn cữ 4h 1 lần, có khả năng con sẽ bị mệt vào cuối ngày và bỏ ăn bữa cuối.
          Phải làm thế nào? Đây là lý do bài ghi trong vòng 3 tới 4 tiếng nếu đói thì cho bình. 1 ngày
          của bé có khả năng sẽ như sau: 7h dậy, 8h mời bình, 11h30(hoặc 12h, 3.5h hoặc 4h) mời bình, 15h30
          mời bình, 19h mời bình, đi ngủ. Thế là đủ 4 cữ 1 ngày trong 12 tiếng. Tuỳ theo nhu cầu mà làm.
          Trong trường hợp khóc ko thể dỗ (TV, điện thoại, chơi, tắm, ngủ v.v. thì cho ăn luôn). Cũng có thể
          cho ngủ dậy rồi ăn sau, bé đói chỉ ngủ được 30f 1 tiếng thôi. Nếu bé ngủ nap mà lố giờ ăn, cho thêm 30f, gọi
          dậy tỉnh táo cho ăn luôn. Không nên cho ngủ ngày quá nhiều đêm sẽ khó ngủ.
        </li>
        <li class="_2cuy _509q _2vxa">Nếu dậy quá sớm: ví dụ 6h dậy, mà 4h tỉnh đòi ăn, thì nên dỗ ngủ đi,
          dỗ ko được, cho ăn ít ít tránh ảnh hưởng ban ngày. 4h tỉnh đòi ăn, mà bình thường 7h dậy, thì
          có thể cho ăn vì sáng ra phải chờ 1 tiếng, khi nào đói mới cho bình (sẽ đủ 3-4 tiếng mới được
          mời). Xem bài 9 để biết tuổi nào nên có bao nhiêu cữ đêm, tránh cho ăn đêm làm ban ngày ko đói
        </li>
        <li class="_2cuy _509q _2vxa">Nếu cho nằm gối thì gối cao 1 xíu, gối em bé thấp quá dễ sặc, mình
          dùng gối ngủ của mình làm gối ăn, Nếu nằm nôi em bé, cẩn thận đầu con gục xuống khó mút. Nếu
          bế bé thì phải ngồi 1 chỗ ko được rung lắc đi lại.
        </li>
        <li class="_2cuy _509q _2vxa">Sau 2 tuần ăn ở chỗ yên tĩnh, nên cho con làm quen với ăn ở chỗ có người
          qua lại. Ăn hóng hớt ko ngoan, dẹp bình. Lần sau đói sẽ chừa. Làm sao mà ở bất kỳ hoàn cảnh
          nào con cũng tập trung ăn là thành công.
        </li>
        <li class="_2cuy _509q _2vxa">Ngày luôn luôn có ngày ăn nhiều ăn ít, bữa nhiều bữa ít. Tổng 1 ngày ko
          xê dịch là được. Trong 1 tuần có ngày nhiều ít là bình thường. Sát luật là sẽ ăn ngoan vượt
          hết bão. 1 tuần thì ăn ngoan, thái độ tốt. 2 tuần là ăn đúng sức lượng sữa ko xê dịch nhiều.
          Dấu hiệu đói có thể ko rõ rệt, chỉ mút tay vì bé hiểu là ra dấu mẹ sẽ cho bình. Miễn thái
          độ tốt là ko sao, thái độ con xấu thì mẹ xem lại mình.
        </li>
      </ul>
      <div class="_2cuy _3dgx _2vxa">Mẹo cho mẹ: Trước khi tập, hãy đọc kỹ hiểu rõ bài ghim. Vì làm sai con
        sẽ ăn kém và gặp nguy hiểm. Đăng bài lên nhóm hỏi nếu chưa hiểu rõ, đăng cập nhật nhờ hỗ trợ và
        sửa sai nếu có.
      </div>
      <div class="_2cuy _3dgx _2vxa">Mẹo cho mẹ: Bàn bạc với người nhà. Hoặc là ôm con cả ngày. Vì có khá
        nhiều mom đang tập cho con thì ông bà hoặc bố lén cho ăn. Nếu con khóc, mẹ có thể vờ như con đã ăn
        rồi nhưng khó ở gắt khóc để người khác ko phá hỏng công sức mẹ tập con ăn trong nhiều ngày (cám
        ơn mom nào đã góp ý nè, mình quên ko lưu lại nên ko nhớ tên).
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">RẤT QUAN TRỌNG: Bạn phải chắc chắn con bạn không có vấn đề sức khoẻ / bệnh tật gì. Mình biết vì con mình đã nằm viện, được khám bs, và khi mới sanh bé ăn rất ngoan. Bé chỉ biếng ăn sau khi bị mẹ ép ăn quá nhiều trong thời gian 6 tuần đầu. Trong thời gian mình áp dụng phương pháp này, bé đều được đi đến bs mỗi tuần để khám và theo dõi. Bé cũng có ống trong trường hợp bé không ăn gì hết mình vẫn có thể dùng ống cho bé ăn. Nếu bạn nào quyết định làm theo phương pháp này, xin hãy để ý việc tiểu tiện của bé, để bé không mất nước và gặp nguy hiểm. Nếu bé có bác sỹ hãy nói chuyện với BS của bé để tiện theo dõi khi cần thiết. </span>
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Cần theo dõi tránh bé ăn quá ít (dưới 300ml trong nhiều ngày). Bé có khả năng hạ đường huyết, mệt mỏi, ngủ li bì, ỉu xìu ko năng động, đói lả. Nếu có dấu hiệu này mẹ nên cho uống nước ngọt có đường, nước cam, kẹo, ăn viên đường/glucose, cho uống sữa như cũ (bú ngủ hoặc cho đồ chơi, đút v.v.) cho ăn tinh bột như cơm, cháo, v.v.</span>
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Sau 1 tuần theo pp này, nếu con vẫn ăn kém, mẹ hãy xem lại có làm sai gì ko? Có ép con ở đâu ko? Nếu chắc chắn ko sai gì, xin mang con đi khám, khả năng là bé bị biếng ăn sinh lý do bệnh nền/ốm/sốt.</span>
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">NẾU MẸ LÀM SAI BẤT KÌ 1 TIỂU TIẾT NÀO, KHẢ NĂNG BÉ SẼ KHÔNG ĂN NHIỀU LÊN VÀ VẪN BỊ SỢ ĂN. TẤT CẢ NHỮNG LUẬT LỆ ĐẶT RA Ở ĐÂY DỀU CÓ NGUYÊN NHÂN LIÊN QUAN ĐẾN NHẬN THỨC VÀ TÂM LÝ CỦA TRẺ, PHẢI ĐỌC KỸ VÀ NHỚ TẤT CẢ. </span>
      </div>
      <div class="_2cuy _3dgx _2vxa">Sách: <span class="_4yxo"
      >Your Baby Bottle Feeding Aversion, Reasons And Solutions</span>. Tác giả Rowena Bennet. Tiếng anh, bạn nào
        muốn có thể mua trên Amazon/Amazon Kindle với giá $10 mỹ và dịch ra để xem. Cuốn sách này nói về 1
        phương pháp ăn gọi là NO PRESSURE - tức là "KHÔNG ÉP," bé được quyền quyết định ăn khi nào và ăn
        bao nhiêu theo sức của bé (Khá giống với BLW, ăn dặm bé chỉ huy). Phương pháp này, mẹ sẽ không ép
        con ăn, mẹ chỉ có nhiệm vụ MỜI con ăn thôi.
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Quan trọng</span>: nếu trong quá trình chữa, và sau quá
        trình chữa bạn vô tình ép bé ăn thì bé sẽ bị sợ ăn lại, và sẽ tiếp tục biếng ăn. Lúc này bạn
        cần bình tĩnh, tiếp tục mời bé ăn theo phương pháp, bé sẽ nắm bắt rất nhanh.
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo"
      >Những điều mình tổng kết được về phương pháp này:</span></div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Mỗi cữ ăn bạn được MỜI bé ăn 2 lần,</span> Lần 2
          cách lần 1 từ<span class="_4yxo"> </span><span class="_4yxo _4yxp">5-20 phút</span><span class="_4yxo"
          >.</span> Nếu lần thứ 2 bé khó chịu, ko ăn, hoặc ăn rất ít thì bỏ hẳn và mỗi cữ chỉ mời 1
          lần (bé nhà mình chỉ ăn khi mời 1 lần). Quá 20f sẽ được tính là cữ sau, không phải trong cùng 1
          cữ nữa.
        </li>
        <li class="_2cuy _509s _2vxa">Cữ kế tiếp tính từ Lần mời 1 của cữ này. Cứ mời là 1 cữ và nên chờ
          3 tới 4 tiếng sau mới mời lại. Phải thoả mãn 2 điều kiện: đói và dãn cữ mới được mời con ăn.
        </li>
        <li class="_2cuy _509s _2vxa">Sau khi mời 1 lần, nếu bé đã ăn và bé tự ngưng lại sau khi bú (hãy để bé tự dừng
          lại nhé, bé là người quyết định nhé), bạn hãy cho bé nghỉ ngơi 5'-20 phút, lúc này <span
            class="_4yxo _4yxp"
          >bạn nên cất bình ra chỗ khác không cho bé nhìn thấy và bế bé ra khỏi vị trí ăn (nếu đang nằm thì bế vác lên, cho ra phòng khác v.v.)</span>.
          Khi nghỉ ngơi, bạn có thể vỗ ợ, lau miệng, chơi với bé, sau đó có thể mời bé lần 2.
        </li>
        <li class="_2cuy _509s _2vxa">Khi bắt đầu chỉ nên có <span class="_4yxo _4yxp">1 người chính</span> cho
          bé ăn - mẹ, để bé dần dần tin tưởng người cho ăn và thoải mái hơn, ko còn sợ hãi khi ăn nữa.
          Sau khi bé đã hết sợ thì có thể để bố hoặc bà cho bé ăn. Còn mẹ nào làm việc và ko thể chăm
          con cả ngày, thì hãy chỉ có thêm <span class="_4yxo _4yxp">1 người phụ</span> cho ăn, bà hoặc bố
          cũng phải hiểu phương pháp này vì chỉ cần 1 lần ép bé ăn là bé sẽ trở về như cũ - biếng ăn.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Tạm ngưng ăn dặm</span>. Nhất là ăn dặm kiểu
          truyền thống (ADTT) vì 2 lý do. 1 là bé đói sẽ chịu ăn sữa. 2 là ADTT giống với việc đút sữa
          ở chỗ bạn ko biết khi nào bé no và tâm lý là bé ăn hết bát thì mới đủ no, việc này vô tình
          tạo sức ép ăn uống cho bé. Khi bé đã ko còn sợ hãi việc ăn bạn có thể bắt đầu cho ăn dặm
          lại, và mình khuyến khích BLW. Nếu thích ADTT, bạn hãy để ý xem bé có hợp tác ăn ko, nếu ko
          hợp tác hãy dừng bữa ăn.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">2 cữ nên cách nhau khoảng 3 đến 4 tiếng</span>
          (tuỳ trường hợp giải thích bên dưới). Nếu bé khóc và mẹ không có cách nào dỗ được thì cho bé
          ăn sau 1 tiếng từ cữ trước.
        </li>
        <li class="_2cuy _509s _2vxa">Ban ngày bạn ko nên cho bé vừa ngủ vừa ăn (mặc dù việc này dễ hơn và
          bé ăn nhiều hơn). Việc ăn lúc tỉnh táo giúp bé liên hệ ăn = no bụng, ko ăn = đói bụng. Bé sẽ
          sẵn sàng ăn khi đói và ăn ngoan hơn.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Không làm bé mất tập trung</span>: Không bao giờ
          cho con chơi đồ chơi, xem tv, bế đi dạo, rung lắc, nằm võng... khi con ăn. Việc này có thể hiệu
          quả 1 thời gian ngắn vì bé mất tập trung và phản xạ bú khiến bé ăn, nhưng khi bé lớn bé tập
          trung cao độ thì đồ chơi TV sẽ ko còn tác dụng nữa.
        </li>
        <li class="_2cuy _509s _2vxa">Tối thiểu 1 ngày bé phải ăn được ít nhât <span class="_4yxo _4yxp">300ml trong 24 tiếng</span>
          đồng hồ để ko gặp nguy hiểm mất nước. Nếu trong những ngày đầu bé ăn ko đủ 300ml lúc tỉnh táo,
          bạn có thể chờ bé ngủ <span class="_4yxo _4yxp">vào ban đêm</span> và cho bé ăn thêm - để được ít
          nhất 300ml. Bạn cần làm theo những điều phía trên, bé từ chối lúc ngủ phải ngưng. Phải chờ ít
          nhất 3 tiếng từ lần ăn trước. Nếu bé ăn đủ 300ml thì cho bé ngủ và thức dậy đòi ăn, khi đó
          bạn có thể MỜI bé.
        </li>
        <li class="_2cuy _509s _2vxa">Nếu 7h mời bé, bé ăn ko đủ 300ml cả ngày, thì cữ kế (3h tới 4h sau cữ
          trước) mẹ chờ mà con ko tỉnh dậy đòi ăn có thể lén cho bình. Nếu bình ko ăn thì cho ti mẹ. Nếu
          đã ăn bình (mà thiếu 300ml) thì ko cho ti mẹ và chờ cữ kế. Luôn luôn mời bình trước. Bình ko ăn
          mới cho ti mẹ.
        </li>
        <li class="_2cuy _509s _2vxa">Cần theo dõi nếu bé ăn ít, vì bé có khả năng <span class="_4yxo _4yxp">hạ đường huyết.</span>
        </li>
        <li class="_2cuy _509s _2vxa">Nếu bé từ chối ăn, lập tức cất bình ra khỏi tầm mắt bé và bỏ bé ra
          khỏi tư thế bế/vị trí ăn. Bé sợ ăn nên khi nhìn thấy bình đó cũng là 1 loại áp lực. Bạn muốn
          bé hiểu là bạn tôn trọng quyết định của bé, và bé phải chịu trách nhiệm cho hành động từ
          chối của mình - bị đói lâu hơn
        </li>
        <li class="_2cuy _509s _2vxa">Giúp bé ngủ ngon. Những ngày đầu bé ăn ít sẽ gắt gỏng khó ngủ, bạn
          sẽ phải bế ru hát võng ti giả v.v., làm tất cả để con ngủ đủ giấc. <span class="_4yxo _4yxp">Bé mệt mỏi sẽ ăn kém</span>.
          (không rung lắc mạnh sẽ ảnh hưởng não bộ con)
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo">QUAN TRỌNG NHẤT: KO ÉP BÉ ĂN.</span> Bé được quyền
          quyết định khi nào đói thì ăn và ăn bao nhiêu là đủ, cho dù bạn nghĩ như vậy quá ít nhưng đôi
          khi bé đã ăn đủ rồi.
        </li>
        <li class="_2cuy _509s _2vxa">Những ngày đầu bé sẽ ăn ít, và chỉ ăn đủ để kềm chế cơn đói bụng.
          Nhưng bạn muốn <span class="_4yxo _4yxp">điều chỉnh là thái độ của bé với việc ăn uống.</span>
          Bạn muốn bé không còn sợ nữa. Khi bé đã thật tin tưởng mẹ sẽ ko ép bé ăn và bé thoải mái khi
          nằm vào vị trí ăn, thoải mái khi nhìn thấy bình bé sẽ từ từ ăn nhiều lên.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo">Tuyệt đối ko cho bé chơi với bình.</span> Bạn phải để
          bé hiểu bình là thức ăn, không phải là đồ chơi. Khi bạn đưa bình và bé không ăn vì sợ, bạn cất
          bình đi không cho bé nhìn thấy nữa. Sau nhiều lần liên tiếp không nhìn thấy bình khi từ chối ăn,
          bé sẽ hiểu ra, nếu mẹ đưa bình mà không ăn, thì bình sẽ biến mất, bé sẽ phải nhịn đói đến
          cữ kế tiếp. Bé sẽ ăn khi mẹ đưa bình vào lần tới nếu không muốn bị đói.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo">Mẹo cho mẹ</span>: đợi bao giờ bé có dấu hiệu đói,
          bạn hãy đi pha sữa(- tránh không pha sữa hâm sữa đợi con), hâm sữa (khoảng 5 10 phút ha), trong lúc
          chờ sữa được hâm nóng, bạn hãy đọc lại luật lệ mời ăn để mình ko quên gì (mẹ chụp màn hình
          lại trong phone để xem cho tiện), khi đó bé sẽ đói lâu hơn 1 xíu và khả năng sẽ khóc (mẹ đừng
          lo, con khóc xíu chả sao đâu). Bé đói hơn thì bé sẽ tu nhanh hơn và ăn nhiều hơn. Ban đêm mẹ hãy
          ngủ thật ngon, nếu bé thức giấc hãy dỗ cho con ngủ lại, nếu con khóc mà không chịu ngủ, khi đó
          mẹ đi pha sữa cho con, con khóc xíu không sao cả.
        </li>
      </ol>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Khi nào thì nên mời bé ăn:</span> (xem bài 3 4 có giải
        thích kỹ hơn kèm ví dụ) tuỳ theo dấu hiệu đói và thời gian tính từ cữ trước. Bạn muốn cho con
        <span class="_4yxo _4yxp"
        >ăn theo nhu cầu và dãn cữ (tránh ăn lặt vặt nhiều bữa mà mỗi bữa ăn ít</span>)
      </div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Khi bé vừa thức dậy, bạn có thể vệ sinh cho bé, chờ bé tỉnh táo, và
          chờ bé tỏ dấu hiệu đói bụng. Nếu thức dậy <span class="_4yxo _4yxp">1 tiếng</span> mà bé ko có
          dấu hiệu gì thì hãy mời bé ăn.
        </li>
        <li class="_2cuy _509s _2vxa">Nếu bạn cho bé ăn đêm, và khi thức dậy vẫn chưa đến 3 tiếng từ cữ
          trước, hãy đợi ít nhất 3 tiếng hãy mời bé ăn.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Chờ 3 đến 4 tiếng giữa 2 cữ:</span> Không nên mời
          quá nhiều cữ trong ngày, mời quá nhiều là ép ăn với bé, mời quá nhiều lần cũng dễ dẫn đến
          tình trạng ăn lắt nhắt mà ko ăn thành cữ. Khi bé có dấu hiệu đói, và đã đủ 3 tiếng từ cữ
          trước, bạn có thể mời bé. (Những bé dưới 3 tháng khả năng sẽ đói nhanh hơn, mẹ nên chờ 3-3.5
          tiếng hoặc cho bé ăn sớm hơn nếu bé có dấu hiệu đói bụng rõ rệt. Không nên mời quá sớm, trước
          3 tiếng, nếu bé đòi ăn không nhiệt tình)
        </li>
        <li class="_2cuy _509s _2vxa">Một số bé hiền hơn và ko khóc mếu đòi ăn thì bạn hãy mời bé sau <span
          class="_4yxo _4yxp"
        >tối đa  4 tiếng từ cữ trước vào ban ngày.</span></li>
        <li class="_2cuy _509s _2vxa">Hãy cố gắng chờ <span class="_4yxo _4yxp"
        >ít nhất 1 tiếng giữa 2 cữ</span><span class="_4yxp"> </span>ăn nếu bé khó chịu khóc mếu vì đói.
          Mời bé quá nhiều lần cũng được xem là 1 dạng ép ăn/làm phiền đối với bé. Trong trường hợp bé
          khóc quấy và bạn không có cách nào dỗ được (đi chơi, ngủ, tắm, ti giả, các hoạt động vui
          vẻ...) thì bạn có thể mời bé ăn trước 1 tiếng.
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Tóm lại: bạn muốn bé ăn giãn cữ, </span>mỗi cữ
          cách khoảng 3 hoặc 4 tiếng từ cữ trước. Ví dụ, bé ăn lúc 7h, 8h bé khóc thì hãy dỗ nín thay
          vì mời ăn. Nếu ăn 7h mà 10h bé khóc, đã đủ 3 tiếng từ cữ trước, bạn hãy mời bé ăn. Ban đêm
          nếu bé ăn đủ 300ml, để bé tự thức dậy đòi ăn. Nếu ăn ít hơn 300ml, mà bé ngủ ko dậy, hãy lén
          cho ăn sau 4 tiếng từ cữ trước để bé ăn đủ 300ml.
        </li>
        <li class="_2cuy _509s _2vxa">Nên nhớ mỗi cữ bạn chỉ mời bé 2 lần thôi. Lần đầu cách lần sau 5-20
          phút. <span class="_4yxo _4yxp">Nếu lần mời thứ 2 bé khó chịu, ăn ít hoặc ko ăn thì bạn có thể bỏ luôn, chỉ mời bé 1 lần mỗi cữ ăn.</span>
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp"
        >Ban đêm hãy để bé ngủ và tự tỉnh dậy đòi ăn.</span> Nếu bé tỉnh quá nhiều lần trong đêm, hãy
          ráng ru bé ngủ, và chỉ cho bé ăn mỗi 3-4 tiếng. Bạn muốn bé ăn vào ban ngày chứ ko phải vào
          ban đêm. Nếu Bé dậy khóc, có thể mở đèn ngủ cho nhìn bình rồi cho ăn. Nếu ko nhìn bình cũng
          chả sao, miễn tự dậy đòi ăn. Nếu ban đêm đang ăn mà ngủ cũng ko sao.
        </li>
      </ol>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Chỗ cho bé ăn:</span> Khi mới bắt đầu hãy chọn 1 nơi im
        lặng ít tiếng ồn và không có người làm phiền để bé tập trung ăn. Nếu tư thế cũ làm bé sợ, hãy
        chọn 1 tư thế mới thoải mái cho cả 2 mẹ con. Không nên hạn chế giữ tay chân bé, hãy để bé có tự
        do hoạt động, điều này giúp mẹ nhìn ra bé có hợp tác hay không rõ ràng hơn. Mình chọn cho bé
        nằm trên gối nhìn thẳng vào mình. Cũng có thể cho bé nằm nghiêng, đôi lúc bé mút mạnh sữa chảy
        nhiều, thì lượng sữa dư ra bé không kịp nuốt sẽ trào ra mép thay vì làm bé sặc.
      </div>
      <div class="_2cuy _3dgx _2vxa">Sau khi tập ăn khoảng 2 tuần bé đã ăn ngoan, mẹ nên cho bé ra nơi có người
        qua lại, phòng khách, v.v. cho ăn để con quen đi, và sau này đi đâu cũng tập trung ăn được. Khi ra 1
        chỗ cho ăn có nhiều người/âm thanh có thể con sẽ ăn ít 1 2 hôm, ko sao, bữa sau con đói sẽ ăn ngoan
        trở lại.
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Khi mời bé ăn, hãy nhìn bé xem bé từ chối hay hợp tác:</span>
      </div>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo _4yxp">Dấu hiệu từ chối:</span></div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Ngậm miệng</li>
        <li class="_2cuy _509s _2vxa">Quay mặt đi, giấu mặt ko nhìn bình</li>
        <li class="_2cuy _509s _2vxa">Bạn đưa bình, bé lơ đãng nhưng lảng tránh nhìn theo bình</li>
        <li class="_2cuy _509s _2vxa">Ko mút</li>
        <li class="_2cuy _509s _2vxa">Dùng lưỡi đẩy núm, nhai núm</li>
        <li class="_2cuy _509s _2vxa">Dùng tay đẩy bình đi</li>
        <li class="_2cuy _509s _2vxa">Đặt vào gối ăn và bé lăn đi hoặc gượng dậy</li>
        <li class="_2cuy _509s _2vxa">Khóc lóc, ưỡn người, hét lên, là dấu hiệu sau cùng vì bố mẹ ko nhận
          ra những dấu hiệu từ chối lúc trước của bé
        </li>
      </ol>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo _4yxp">Dấu hiệu hợp tác:</span></div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Nhìn thẳng vào bình, nhìn theo bình</li>
        <li class="_2cuy _509s _2vxa">Mở miệng - chấp nhận bình khi bố mẹ <span class="_4yxo">từ từ</span> đưa
          núm đến gần miệng bé
        </li>
        <li class="_2cuy _509s _2vxa">Bé lớn hơn có thể dùng tay với bình và đưa vào miệng</li>
        <li class="_2cuy _509s _2vxa">Bé chịu để bình trong miệng</li>
        <li class="_2cuy _509s _2vxa">Bé chịu mút</li>
      </ol>
      <ul class="_5a_q _5yj1" dir="ltr">
        <li class="_2cuy _509q _2vxa">Nếu bé hợp tác, bạn hãy <span class="_4yxo">từ từ đặt núm vào miệng bé</span>
          hoặc để bé tự làm nếu bé đủ lớn.
        </li>
        <li class="_2cuy _509q _2vxa">Nếu bé từ chối, <span class="_4yxo _4yxp">lập tức giấu bình không để bé thấy và bế bé đi chỗ khác</span>,
          chờ lần sau mời. Nên nhớ bình và tư thế ăn làm bé sợ, bé đã từ chối mà vẫn thấy bình và
          nằm ở tư thế ăn, <span class="_4yxo _4yxp">bé sẽ nghĩ mình vẫn còn phải ăn tiếp</span>, sẽ ảnh
          hưởng đến tâm lý của bé, gây kéo dài sợ hãi.
        </li>
        <li class="_2cuy _509q _2vxa">Nếu bé ko tỏ thái độ gì, hãy thử dùng núm bình cạ nhẹ vào môi bé
          xem bé có muốn ăn không.
        </li>
      </ul>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Bé ko tỏ thái độ rõ ràng khi mời ăn:</span></div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Bé há miệng, ngậm ti, <span class="_4yxo _4yxp"
        >mút một ít rồi nhả ra</span>. Bé lặp lại hành động nhiều lần. Mẹ không rõ bé muốn ăn hay từ
          chối. Khi gặp trường hợp này bạn nên ngừng lại, đừng vì con mút thêm 1 ít mà cố gắng. Bạn
          ngưng lại bé sẽ hiểu nếu bé ko ăn ngoan và ăn nhanh thì bé sẽ ko được ăn nữa. Lần tới bé sẽ ăn
          tốt hơn.
        </li>
        <li class="_2cuy _509s _2vxa">Nếu bạn không rõ ràng bé có muốn ăn hay không vì bất kì lý do hay hành
          động nào của bé, tốt nhất là ngưng bữa ăn.
        </li>
        <li class="_2cuy _509s">
          <div class="_2cuy _3dgx _2vxa">Lý do bé ko tỏ rõ thái độ:</div>
          <ol class="_5a_q _509r" dir="ltr">
            <li class="_2cuy _509s _2vxa">Bé mệt. Bé đói và muốn ăn nhưng bé cũng mệt và muốn ngủ, nên bé
              bị rối loạn và ko rõ mình nên làm gì trước. Bạn phải để ý giấc ngủ của bé. Bé ngủ đủ
              giấc sẽ ăn tốt và ngoan hơn. <span class="_4yxo _4yxp">Nếu giờ ăn trùng giở ngủ, cho bé ngủ dậy rồi ăn sau. Cần làm mọi cách ru bé ngủ, thiếu ngủ sẽ ăn kém.</span>
            </li>
            <li class="_2cuy _509s _2vxa">Bé bệnh/bị đau: bé bị đau sẽ khó chịu, bé muốn ăn nhưng việc ăn
              làm bé đau. Bạn cần chắc chắn bé khoẻ mạnh ko bệnh tật trước khi bắt đầu
            </li>
            <li class="_2cuy _509s _2vxa">Núm bị bẹt lại: một số bé mút mạnh làm núm bị bẹt lại và bé
              không mút được, mẹ nên xem xét cỡ núm vừa tuổi bé.
            </li>
            <li class="_2cuy _509s _2vxa">Núm quá to và sữa chảy quá nhanh sẽ làm bé sợ, cho nằm nghiêng nếu
              mẹ vừa tăng cỡ núm, sữa chảy nhanh sẽ tràn ra mép
            </li>
          </ol>
        </li>
        <li class="_2cuy _509s _2vxa">Bé đang ăn thì ngưng lại 1 cách bình tĩnh: nếu bé ko tỏ thái độ khó
          chịu gay gắt, nếu bé ngưng lại và nhìn ngó xung quanh vì tò mò (rất bình thường), bạn có thể
          đưa bình vào tầm mắt bé để bé nhìn thấy và xem bé còn muốn ăn tiếp ko. Nếu bé từ chối hãy
          dừng bữa ăn và cất bình. Không bao giờ ngọ ngoạy núm trong mồm bé, không gõ bình, không ghì
          bình trong mồm, không làm bé khó chịu và vô tình gây áp lực ăn cho bé.
        </li>
        <li class="_2cuy _509s _2vxa">Một số bé cầm bình và rút ra rồi lại bỏ vào miệng nhiều lần, nhai,
          cắn, lấy lưỡi đảo núm: bé chơi với bình, nếu bé chơi nghịch bình tĩnh không khó chịu, hãy để
          bé chơi 1o giây. Sau 1o giây, nếu bé chỉ chơi mà không có vẻ muốn ăn nữa, ngưng bữa ăn và cất
          bình đi. Việc nghịch bình khác với việc bé nhả bình ra lấy hơi (tay bé vẫn ghị chặt bình) và
          kéo bình vào mút nhiều, mút nhanh. Nếu núm ko thích hợp, bé có thể mút mạnh, xẹp núm, nên
          nhả ra để chờ núm phình lên mới kéo vào mút tiếp. Nếu núm quá to, bé có thể nghỉ ngơi vì
          mút nhiều làm bé mệt. Nếu bé bị bệnh nghẹt mũi, bé ngưng lại để thở. (mẹ xem bài 6 cỡ núm
          và 7 bé bị bệnh.
        </li>
      </ol>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Vài điều cần lưu ý:</span></div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Ban ngày khi cho bé ăn không được để bé vừa ăn vừa ngủ, nếu bé bắt đầu
          buồn ngủ, hãy vuốt chân tay bé, giúp bé tỉnh táo. Có thể nói chuyện với bé từ đầu bữa ăn và
          giữ bé tỉnh táo
        </li>
        <li class="_2cuy _509s _2vxa">Không dụ dỗ bé ăn bằng ti giả (cho bé mút ti giả lừa bé và thay bằng
          bình sau đó)
        </li>
        <li class="_2cuy _509s _2vxa">Không lẫn lộn bình nước/bình sữa, bé sẽ nghĩ mẹ lừa bé.</li>
        <li class="_2cuy _509s _2vxa">Hãy quan sát thái độ của bé, nếu bé lấy lưỡi đẩy bình ra, lấy tay
          gạt bình, bạn hãy ngưng bữa ăn.
        </li>
        <li class="_2cuy _509s _2vxa">Không nên giữ cằm bé và ép bé tiếp tục ngậm bình</li>
        <li class="_2cuy _509s _2vxa">Không ghị chặt bình, để bé có cơ hội nhè bình/gạt bình nếu bé ko
          muốn ăn
        </li>
        <li class="_2cuy _509s _2vxa">Bé đang ăn thì ngưng mút và quay ngoắt đi, bạn đừng xoay bình theo miệng
          bé và giữ bình trong miệng bé, có khả năng bé đã ko muốn ăn nữa, hãy cất bình đi, chờ lần
          mời sau hoặc cữ sau.
        </li>
        <li class="_2cuy _509s _2vxa">Khi mời bé chỉ nên đưa bình vào tầm mắt bé, nếu bé quay đầu đi từ
          chối, hãy dừng bữa ăn, không cầm bình xoay theo đầu bé ép bé phải nhìn bình. Nếu bạn không
          chắc chắn bé đã nhìn thấy bình, chỉ nên đưa bình vào tầm mắt bé thêm 1 lần thôi, nếu bé tiếp
          tục quay đi tránh bình tức là bé không muốn ăn đâu.
        </li>
        <li class="_2cuy _509s _2vxa">Nếu bé khóc to khi bạn đặt bé vào tư thế cho ăn, hãy dỗ cho bé nín
          trước khi đưa bình cho bé nhìn.
        </li>
        <li class="_2cuy _509s _2vxa">Một số bé sẽ khóc to và nhắm mắt khi quá đói, l<span
          class="_4yxo _4yxp"
        >úc này bé sẽ không nhìn thấy bình</span>, bạn có thể cạ/chà nhẹ núm vào miệng bé để xem bé
          có muốn ăn hay không. Cũng có thể nhỏ sữa vào miệng bé
        </li>
        <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Không mời bé quá nhiều lần</span>. Hãy nhớ chỉ 2
          lần mỗi cữ ăn (cách 5'-20 phút), mỗi cữ ăn cách nhau tốt nhất là 3-4 tiếng, ban đêm không đánh
          thức bé cho ăn nếu đã ăn đủ 300ml. để bé tự dậy ăn khi có nhu cầu
        </li>
        <li class="_2cuy _509s _2vxa">Không cho bé xem tv/đồ chơi để bé mất tập trung</li>
        <li class="_2cuy _509s _2vxa">Nếu bé muốn cầm đồ chơi, hãy cho bé cầm 1 món đồ chơi nhỏ, 1 vài bé
          có thói quen quơ quơ tay, hay vô tình gạt đổ bình, cho bé cầm 1 món đồ chơi nhỏ sẽ giúp bé ko
          quơ tay nữa. Mẹ không được cầm đồ chơi rung lắc làm con mất tập trung. Mẹ có thể nói chuyện với
          con giúp con thả lỏng tâm tình, nhưng không nên hát hò chọc ghẹo làm bé mất tập trung. (quơ tay
          khác với dùng tay rút/hất bình ra khỏi miệng)
        </li>
        <li class="_2cuy _509s _2vxa">Khi bé vẫn còn mút hãy để bé tự dừng lại, đừng rút bình ra để vỗ
          ợ, lau mặt, ..
        </li>
        <li class="_2cuy _509s _2vxa">Hãy giữ bé ngủ điều độ, thức dậy cùng 1 giờ mỗi ngày để đồng hồ
          sinh học của bé ổn định giấc ăn/ngủ
        </li>
        <li class="_2cuy _509s _2vxa">Khi bắt đầu, chỉ nên để 1 người cho bé ăn (mẹ), tối đa 2 người. Sau khi
          bé đã không còn dấu hiệu sợ hãi, bố hoặc bà có thể cho bé ăn. Tất cả mọi người đều phải
          biết cách mời bé ăn và không được cố ép bé ăn hết bình. Chỉ cần 1 lần, bé sẽ dễ dàng sợ ăn
          trở lại.
        </li>
        <li class="_2cuy _509s _2vxa">Không bỏ mùi vị lạ vào sữa bé trong lúc thực hiện phương pháp này và
          bé vẫn chưa hết sợ ăn. Không nên cho bình lúc thì sữa mẹ lúc thì sữa bột. Tốt nhất trong quá
          trình tập chỉ nên có 1 loại sữa.
        </li>
        <li class="_2cuy _509s _2vxa">Vài bé cảm nhận nhiệt độ tốt và muốn sữa ở 1 nhiệt độ nhất định.
          Nếu hâm sữa thì hãy hâm sữa cả 2 lần mời. Mình để sữa ở nhiệt độ phòng vì ko cần lo lắng về
          nhiệt độ khác nhau khi hâm sữa
        </li>
        <li class="_2cuy _509s _2vxa">Hãy dùng 1 loại bình, không thay đổi nhiều loại bình khi thực hiện
          phương pháp. Mỗi loại bình có núm và cách mút khác nhau, thay đổi nhiều làm loạn bé.
        </li>
        <li class="_2cuy _509s">
          <div class="_2cuy _3dgx _2vxa">C<span class="_4yxo _4yxp">hấm điểm thái độ trong bữa ăn của bé</span>.
            (mình dịch từ tiếng anh, ngôn từ không chau truốt lắm)
          </div>
          <ol class="_5a_q _509r" dir="ltr">
            <li class="_2cuy _509s _2vxa">1 điểm: bé khóc thét giãy giụa không muốn được bế cho ăn hoặc khi
              bạn cho bé nhìn bình sữa bé lập tức khóc, ưỡn người, gắt gỏng từ chối
            </li>
            <li class="_2cuy _509s _2vxa">2 điểm: thái độ bé ko rõ ràng, bé khóc quấy trong bữa ăn, đang mút
              thì đột ngột dừng lại và tâm trạng bé có vẻ khó chịu
            </li>
            <li class="_2cuy _509s _2vxa">3 điểm: bé có vẻ bồn chồn không yên khi ăn, bé dừng mút đột ngột
              và có thái độ căng thẳng
            </li>
            <li class="_2cuy _509s _2vxa">4 điểm: bé hơi hơi khó chịu khi mút, đôi lúc bé mất tập trung khi
              mút, bé dừng bữa ăn 1 cách bình tĩnh
            </li>
            <li class="_2cuy _509s _2vxa">5 điểm: bé ăn/mút với tâm trạng thoải mái, bé dừng bữa ăn 1 cách
              bình tĩnh
            </li>
            <li class="_2cuy _509s _2vxa">Nếu bạn cho bé ăn, và điểm của bé mỗi bữa ăn là 4 hoặc 5, thì bạn
              đã thành công chữa bệnh sợ ăn cho bé nhé. Nên nhớ thái độ ăn của bé được cải thiện trước,
              sau đó mới đến lượng sữa. Bé có thể sụt cân trong quá trình, và bé sẽ ăn nhiều lên từ từ
              gỡ lại cân đã mất.
            </li>
            <li class="_2cuy _509s _2vxa"><span class="_4yxo _4yxp">Nếu sau 5 đến 7 ngày mà thái độ ăn của bé vẫn không khá lên, hãy xem lại cách bạn mời bé ăn, đôi lúc bạn vô tình ép bé ăn mà không biết</span>
            </li>
          </ol>
        </li>
      </ol>
      <div class="_2cuy _3dgx _2vxa"><span class="_4yxo">Khi thực hiện phương pháp này phải cẩn thận:</span>
      </div>
      <ol class="_5a_q _509r" dir="ltr">
        <li class="_2cuy _509s _2vxa">Một ngày tối thiểu bé cần 300ml sữa để không mất nước. Nếu bé liên
          tục ăn chỉ 300ml trong nhiều ngày, phải xem xét vấn đề bé mất nước nhé. Thường thì chỉ 1 tuần
          bé sẽ ăn ổn (500 600ml nếu mẹ theo đúng luật. Nếu bé vẫn ăn ít mẹ hãy xem lại mình có sai
          luật ko? Hoặc bé có ốm sốt mọc răng ko. Nếu sau 1 tuần mà lượng sữa vẫn không tăng, mẹ chắc
          chắn mình ko làm sai ko ép con, thì hãy mang con đi khám, có khả năng là bệnh sinh lý chứ ko
          phải tâm lý)
        </li>
        <li class="_2cuy _509s _2vxa">Hãy ghi nhớ số lần bé đái, nhiều hay ít, xem xét bé có mất nước
          không, bạn có thể cho bé uống nước trong cốc sippy để bổ sung nước giữa 2 cữ ăn. Không cho bé
          uống nước trong bình, hãy để bé hiểu bình là dành cho sữa. Nếu bé không hợp tác uống nước,
          không nên ép bé.
        </li>
        <li class="_2cuy _509s _2vxa">Chắc chắn bé không có bệnh nền, bé đã từng ăn uống tốt khi vừa sinh
        </li>
      </ol>
    </div>
  </div>
</template>


